.main {
    max-width: 100%;
    //   overflow: hidden;
    &--header {
      font-style: normal;
      font-weight: bolder;
      line-height: 1.2;
      font-family: "Syncopate", "sans-serif";
      text-transform: uppercase;
      @include fluid-font(2rem, 3rem);
      margin: 0 0 2.625rem 0;
    }
    &--normal {
      @include fluid-font(1rem, 1rem);
      margin: 0.5rem 0 2.5rem 0;
      font-weight: normal;
    }
    &--text {
      @include fluid-font(1.1rem, 1.5rem);
      max-width: 48.8125rem;
      margin: 1.5rem auto;
    }
    &--italic {
      font-style: italic;
      max-width: 48.8125rem;
      margin: 1.5rem auto;
    }
    &--green {
      color: $green;
    }
    /* title and desc: all pages - font */
    &__page {
      &--title {
        @include fluid-font(1rem, 1.5rem);
        font-weight: normal;
      }
      &--desc {
        @include fluid-font(1.3rem, 3rem);
        font-family: "Syncopate", "sans-serif";
        text-transform: uppercase;
        margin: 5rem 0;
      }
    }
  
    &__intro {
      display: flex;
      flex-flow: column nowrap;
      justify-content: center;
      align-items: center;
      padding-top: 90px;
      margin: 0 auto;
      max-width: 100%;
  
      & img {
        width: 100%; //Analysis
        max-width: 100%;
      }
  
      @media screen and (max-width: 1024px) {
        & img {
          width: 100%;
          display: flex;
          justify-content: center;
        }
      }
    }
  
    @media screen and (min-width: 1256px) {
      &__about {
        display: flex;
        margin: 5rem 0;
        flex-flow: row wrap;
        padding-right: 6rem;
        // height: 100vh;
      }
      &__avatar {
        width: 100%;
        flex: 1;
      }
      &__me {
        width: 50%; /* definir largura // old: 21.5vw */
        padding: 1.5rem 20rem 1.5rem 5rem; /* olds values: margin: 0 18.5vw 0 10vw; */
        box-sizing: border-box;
  
        &--grid {
          width: 414px;
        }
      }
