    &__avatar img {
      width: 100%;
      max-width: 802px;
    }
    /* Service */
    &__works {
      margin: 0 auto;
      display: flex;
      flex-flow: column wrap;
      text-align: center;
      justify-content: center;
      padding: 8rem 2.5rem 5rem 4rem;
  
      &--figure {
        margin: 6rem 0 12rem 0;
      }
  
      > figure > h1 {
        margin: 5rem 0 8rem 0;
      }
  
      &--grid {
        padding: 0 26.5vw;
        margin-bottom: 8rem;
      }
  
      &--page {
        width: 560px;
        margin: 10rem auto;
  
        & p {
          line-height: 1.5;
        }
      }
  
      &--risk {
        text-decoration: line-through;
      }
    }
  
    @media screen and (max-width: 560px) {
      &__works--page {
        width: auto;
        padding: 0 1.75rem;
        margin-bottom: 5rem;
      }
    }
    &__view--pic {
      width: 100%;
      // display: block;
    }
    &__service--title {
      font: normal normal bolder 1.5rem "Syncopate", "sans-serif";
      text-transform: uppercase;
    }
    @media screen and (max-width: 1255px) {
      &__works {
        margin: 0 auto;
        display: block;
        padding: 0 1.5rem 10rem 1.5rem;
        text-align: center;
  
        &--grid {
          padding: 0 3rem 6rem 3rem;
          margin-bottom: 0;
        }
      }
      &__view--pic {
        width: 100%;
        justify-content: center;
      }
      &__view {
        margin-top: 0.25rem;
      }
    }
    &__view {
      margin-top: 3.75rem;
    }
  
    /* blog */
    &__blog {
      display: block;
      padding: 0 3.4vw;
    }
  }
  