.menu {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 3.4vw;
  width: 100%;
  height: 120px;
  position: fixed;
  top: 0;
  background-color: $beige;
  z-index: 2;

  &__logotype--mobile {
    display: none;
  }

  &--mobile {
    display: none;
  }

  & label[for="menu__mob"] {
    display: none;
    cursor: pointer;
  }

  &__navigation {
    list-style: none;
    text-transform: uppercase;
    font-family: "Syncopate", "Sans-serif";
    @include fluid-font(0.75rem, 1.5rem);
    font-weight: bolder;
    margin: 0;
    padding: 0;
  }

  &__item {
    display: inline-block;
    padding: 20px 40px;
    color: $black;
  }
